import common from'../../util/common.js'
import code from "../../util/code.js"
import qs from"../../components/qs.js"
export default {
	data() {
		return {
			name:'',//姓名
			nameE:'',
			tel:'',//手机号
			telE:'',
			card:'',//身份证
			cardE:'',
			section:'',//部门
			sectionE:'',
			job:'',//工作
			jobE:'',
			duty:'',//职务
			dutyE:''
		};
	},
	onLoad() {
		var examId = uni.getStorageSync("examIdS"); 
		if (examId == "" || examId == undefined || examId == null || examId == NaN || examId == 'NaN') {
			uni.reLaunch({
				url:'../index/index?mark =' + uni.getStorageSync("markS")
			})	
		}else{
			var joinI = uni.getStorageSync("joinInfoS");
			if(joinI == 0){
				return;
			}else{
				console.log(joinI.indexOf("姓名"));
				if(joinI.indexOf("姓名") >= 0){
					this.nameE = 0
					console.log(this.nameE);
				}else{
					this.nameE = 1
				}
				if(joinI.indexOf("手机号") >= 0){
					this.telE = 0
				}else{
					this.telE = 1
				}
				if(joinI.indexOf("部门单位") >= 0){
					this.sectionE = 0
				}else{
					this.sectionE = 1
				}
				if(joinI.indexOf("身份证") >= 0){
					this.cardE = 0
				}else{
					this.cardE = 1
				}
				if(joinI.indexOf("工号") >= 0){
					this.jobE = 0
				}else{
					this.jobE = 1
				}
				if(joinI.indexOf("职务") >= 0){
					this.dutyE = 0
				}else{
					this.dutyE = 1
				}
			}
		}
		
	},
	methods: {
		//开始考试
		startBtn:function(e){
			var dataV = {}//添加数组
			if(this.nameE == 0){
				if(this.name == ""){
					uni.showToast({
						title: "请输入姓名",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'姓名',this.name)
				}
			} 
			if(this.telE == 0){
				if(this.tel == ""){
					uni.showToast({
						title: "请输入手机号",
						icon: 'none',
						duration: 2000
					})
					return
				}else if(code.validatePhoneNumber(this.tel) == false){
					uni.showToast({
						title: "请输入正确的手机格式",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'手机号',this.tel)
				}
			}
			if(this.cardE == 0){
				if(this.card == ""){
					uni.showToast({
						title: "请输入身份证号",
						icon: 'none',
						duration: 2000
					})
					return
				}else if(code.IdentityCodeValid(this.card)){
					uni.showToast({
						title: "请输入正确的手机格式",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'身份证号',this.card)
				}
			}
			if(this.sectionE == 0){
				if(this.section == ""){
					uni.showToast({
						title: "请输入部门单位",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'部门单位',this.section)
				}
			}
			if(this.jobE == 0){
				if(this.job == ""){
					uni.showToast({
						title: "请输入工号",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'工号',this.job)
				}
			}
			if(this.dutyE == 0){
				if(this.duty == ""){
					uni.showToast({
						title: "请输入职务",
						icon: 'none',
						duration: 2000
					})
					return
				}else{
					this.$set(dataV,'职务',this.duty)
				}
			}  
			console.log(dataV);
			var examIdV = uni.getStorageSync("examIdS"); 
			var userId = uni.getStorageSync("userIdS"); 
			uni.request({
				url: common.port.mJoin,
				header: {
					'content-type': 'application/json; charset=utf-8',
				},
				method: "POST",
				dataType: "json",
				data:{
					"user_id":userId,
					"exam_id":examIdV,
					"content":dataV
				},
				success: (result) => {
					console.log(result.data);
					if(result.data.code == 500){
						uni.showToast({
							title: result.data.msg,
							icon: 'none',
							duration: 2000
						})
						return
					}else{
						uni.navigateTo({
							url:'../simulate/simulate'
						})
					}
				}
			})
		},
		back:function(e){
			uni.reLaunch({
				url:"../notice/notice"
			})
		}
	}
};
